/* eslint-disable jsx-a11y/iframe-has-title */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import ShowMoreText from "react-show-more-text";
import { useParams,useNavigate, Link } from 'react-router-dom'
import imbd from '../../image/imbd.png'
import popularity from '../../image/popularity.png'
import movieImg from "../../image/movie.jpg"
import avatarImg from "../../image/avatar.jpg"

export default function MovieDetails() {
    let {id} = useParams()           //to recive the id in the url
    let navigate = useNavigate();
    let[movie,setMovie]=useState(null)
    let [isLoading, setIsLoading] = useState(false)
    let[pageTitle,setPageTitle]=useState('')
    let[movieVideo,setMovieVideo]=useState(null)
    let[movieCreditsCast,setMovieCreditsCast]=useState(null)
    let[movieCreditsCrew,setMovieCreditsCrew]=useState(null)
    let[movieSimilar,setMovieSimilar]=useState(null)
    let[showMoreCast,setShowMoreCast]=useState(6)
    let[showMoreCrew,setShowMoreCrew]=useState(6)
    let[showMoreSimilar,setShowMoreSimilar]=useState(6)
    let[showMoreTextCast,setShowMoreTextCast]=useState("Show More")
    let[showMoreTextCrew,setShowMoreTextCrew]=useState("Show More")
    let[showMoreTextSimilar,setShowMoreTextSimilar]=useState("Show More Similar")

    useEffect(()=>{
        getMovieDetails(id)
        getMovieVideo(id)
        getMovieCreditCast(id)
        getMovieCreditCrew(id)
        getMovieSimilar(id)
        setShowMoreCast(6)
        setShowMoreCrew(6)
        setShowMoreSimilar(6)
        setShowMoreTextCast("Show More")
        setShowMoreTextCrew("Show More")
        setShowMoreTextSimilar("Show More Similar")
        document.title=`${pageTitle}`   //to show the title in the window
    },[id,pageTitle])

    async function getMovieDetails(movieId){
        setIsLoading(true)
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        setIsLoading(false)
        setMovie(data)
        setPageTitle(data.title)
    }
    async function getMovieVideo(movieId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        setMovieVideo(data.results)
    }
    async function getMovieCreditCast(movieId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        let unique = [...new Map(data.cast.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setMovieCreditsCast(order)
    }
    async function getMovieCreditCrew(movieId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}/credits?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        let unique = [...new Map(data.crew.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setMovieCreditsCrew(order)
    }
    async function getMovieSimilar(movieId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}/similar?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US&page=1`)
        let unique = [...new Map(data.results.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setMovieSimilar(order)
    }
    async function showMoreCastToggle(){
        if(showMoreCast === 6 && showMoreTextCast === "Show More"){
          setShowMoreCast(9999)
          setShowMoreTextCast("Show Less")
        }
        else{
          setShowMoreCast(6)
          setShowMoreTextCast("Show More")
        }
    }
    async function showMoreCrewToggle(){
        if(showMoreCrew === 6 && showMoreTextCrew === "Show More"){
          setShowMoreCrew(9999)
          setShowMoreTextCrew("Show Less")
        }
        else{
          setShowMoreCrew(6)
          setShowMoreTextCrew("Show More")
        }
    }
    async function showMoreSimilarToggle(){
        if(showMoreSimilar === 6 && showMoreTextSimilar === "Show More Similar"){
          setShowMoreSimilar(9999)
          setShowMoreTextSimilar("Show Less")
        }
        else{
          setShowMoreSimilar(6)
          setShowMoreTextSimilar("Show More Similar")
        }
    }

  return (<>{isLoading? <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><span className="mx-2">Loading</span></h1>:
  <>{movie !== null?     
  <div className="container container-fluid" style={{ backgroundImage : `linear-gradient(rgba(0, 0, 0, 0.338),rgba(0, 0, 0, 0.338)),url(https://image.tmdb.org/t/p/w500${movie.backdrop_path})`, backgroundRepeat:'no-repeat' ,backgroundSize:'100%'}}>   
  
    <div className="row my-4">
        <div className="col-md-4 text-center">
            <div className="borderImgProfile">
                <img src={`https://image.tmdb.org/t/p/w500`+movie.poster_path} alt="" className="w-100" />
            </div>
            <a href={movie.homepage} target="_blank" rel="noreferrer" className="btn btn-warning px-5 py-1 my-3">Watch Now</a>
        </div>
        <div className="col-md-8 gy-3">
            <h3 className='h1 mx-1 my-2'>{movie.title}</h3>
            <p className='h2 mx-1 my-2'>{movie.release_date.slice(0,4)}</p>
            <p className='h5 mx-1 my-2 text-muted'>{movie.tagline}</p>
            {movie.genres.map((e,i)=> <span key={i} className="btn btn-info mx-1 my-2">{e.name}</span>)}
            <div className="vote my-2 mx-1">
                <p className='h2'>{movie.vote_average.toFixed(1)} / 10</p>
                <p className='h5'>Total Votes : {movie.vote_count}</p>
            </div>
            {movie.overview !== false ? <div className="summary my-5">
                <p className='fw-bold'>Plot Summary</p>
                <ShowMoreText lines={3} more="Show more" less="Show less" className="content-css"><p>{movie.overview}</p></ShowMoreText>
            </div>: ""}
            
            <div className="logo d-flex">
                <div className='logoImg m-2'>
                <a href={`https://www.imdb.com/title/${movie.imdb_id}/`} target="_blank" rel="noreferrer" title='IMDB Page'>
                    <img src={imbd} alt="" className='w-100' />
                </a>
            </div>
            <div className='logoImg m-2'>
                <img src={popularity} alt="" className='w-100 d-inline-block' title='Popularity' />
                {Math.floor(movie.popularity)}
            </div>
            </div>
        </div>
    </div>




{movieVideo !== null?<>
{movieVideo.length > 0?<>
<h2>Watch Trailer</h2>
    {movieVideo.map((video,i)=>
<>{video.type==="Trailer" ? 
<div className='row'>
    <iframe className='col-md-10 mx-auto my-3' allowfullscreen="allowfullscreen" width="420" height="345" src={`https://www.youtube.com/embed/${video.key}?controls=1`} ></iframe>
    </div>:""}</>)} 
</> :""}</>:""}





<div className="row g-3 my-4">
    
    {movieCreditsCast !== null ? <>
    {movieCreditsCast.length > 0 ?<h2>Cast</h2> : ""}
        {movieCreditsCast.map((movieCast,i)=> 
            <Link to={`/peopledetails/${movieCast.id}`} key={i} className="col-md-2">
                <div className="item">
                    <div className="borderImg position-relative">
                        <img src={movieCast.profile_path !== null ? `https://image.tmdb.org/t/p/w500${movieCast.profile_path}` : avatarImg} alt="" className="w-100" />
                        <div className="layer position-absolute">
                            <div className="d-flex justify-content-center align-items-center">
                                <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
                            </div>
                        </div>
                    </div>
                    <h3 className='h6 fw-bold'>{movieCast.name}</h3>
                </div> 
            </Link>
        ).slice(0,showMoreCast)}
    
    </>:""}
    <div className="row">
    <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreCastToggle}>{showMoreTextCast}</button>
    </div>
    
    {movieCreditsCrew !== null ? <>
        {movieCreditsCrew.length > 0 ?<h2>Crew</h2> :""}
        {movieCreditsCrew.map((movieCrew,i)=> 
                <Link to={`/peopledetails/${movieCrew.id}`} key={i} className="col-md-2">
                    <div className="item">
                        <div className="borderImg position-relative">
                            <img src={movieCrew.profile_path !== null ? `https://image.tmdb.org/t/p/w500${movieCrew.profile_path}` : avatarImg} alt="" className="w-100" />
                            <div className="layer position-absolute">
                                <div className="d-flex justify-content-center align-items-center">
                                    <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
                                </div>
                            </div>
                        </div>
                        <h3 className='h6 fw-bold'>{movieCrew.name}</h3>
                    </div>
                </Link>
        ).slice(0,showMoreCrew)}
    </>:""}

    <div className="row">
        <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreCrewToggle}>{showMoreTextCrew}</button>
    </div>

    {movieSimilar !== null ? <>
    {movieSimilar.length > 0 ? <>
    <h2>Watch Also</h2>

        {movieSimilar.map((movieSimilar,i)=>
        <>{movieSimilar.popularity >= 1 ?
            <>{movieSimilar.poster_path == null ? <></> : <div className="col-md-2">
        <Link to={`/moviedetails/${movieSimilar.id}`}>
            {movieSimilar.poster_path == null ? 
            <div className='empty'></div> 
            :
            <div className="item">
                <div className="borderImg position-relative">
                    <img src={movieSimilar.poster_path !== null ? `https://image.tmdb.org/t/p/w500${movieSimilar.poster_path}` : movieImg} alt="" className="w-100" />
                    <div className="layer position-absolute">
                        <div className="d-flex justify-content-center align-items-center">
                            <div className='w-100 text-center my-1'>
                                <i className="fa-solid fa-star h2 my-2"></i>
                                <p className='h2 my-2 fw-bold w-100'>{movieSimilar.vote_average.toFixed(1)} / 10</p>
                            </div>
                            <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                        </div>
                </div>
                </div>
                <h3 className='h6 fw-bold'>{movieSimilar.title}</h3>
                <p className='h6'>{movieSimilar.release_date.slice(0,4)}</p>
            </div>}
        </Link></div>}
        </> 
        : null}
        </>).slice(0,showMoreSimilar)}
    {movieSimilar.length > 6 ? 
    <div className="row">
        <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreSimilarToggle}>{showMoreTextSimilar}</button>
    </div>:""}
    </>:""}</>:""}
</div>



<footer className='row position-static bottom-0 justify-content-center'>
    <button className="btn btn-dark col-md-4 my-2" onClick={() => navigate(-1)}><i className="fa-sharp fa-solid fa-arrow-left"></i> Back</button>
</footer>
   </div>:""}</>}</>)
}
