/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { PaginationControl } from 'react-bootstrap-pagination-control';
import { Link } from 'react-router-dom'
import $ from 'jquery'
import { ShareCategory } from '../Navbar/Category';

export default function TV() {
  let {tvCategoryType,changeTVCategory} = useContext(ShareCategory)
  let [isLoading, setIsLoading] = useState(false)
  let [tvList,setTVList] = useState([])
  let [pageNumber,setPageNumber] = useState(1)
  let [totalResult,setTotalResult ]= useState(1)


  useEffect(()=>{
    getData(1,tvCategoryType)
    document.title=`TV Shows - ${tvCategoryType.replace(/_/g,' ')}`
  },[tvCategoryType])

  async function getData(pN=pageNumber,pT='popular'){
    setIsLoading(true)
    let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${pT}?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US&page=${pN}`)
    setIsLoading(false)
    setTVList(data.results)
    setTotalResult(data.total_results)
  }

  async function search(e){
    let value = e.target.value
    if (value !== ''){
      let {data} = await axios.get(`https://api.themoviedb.org/3/search/tv?api_key=8613e4e1776af4e8633cc311d67b3e09&language=en-US&query=${value}&page=1&include_adult=false`)
      setTVList(data.results);
    $(".pagination").hide(300)
    }
    else{
      getData()
      $(".pagination").show(500)
    }
  }

  // function changeType(e){                 //sending a props with a function gets all data of the tag calling it
  //   let moviePageTitle=e
  //   let moviePageTitleMod = moviePageTitle.replace(/_/g,' ')       //e.target getting all the tag, e.target.id getting the value of the id
  //   setPageTitle(moviePageTitleMod)
  //   console.log(moviePageTitleMod);
  // }

  function changeNum(page){
    setPageNumber(page)
    getData(page,tvCategoryType)
  }
  return (    <>{isLoading? 
  <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><span className="mx-2">Loading</span></h1>:
  <div className="container container-fluid container-sm">   <>
  <input className='form-control bg-transparent text-white w-75 my-5 mx-auto' onChange={search} type="text" placeholder='Search For Your TV Show...'/>

  <div className="category d-flex justify-content-center w-100 flex-wrap my-4">
    <span onClick={()=>changeTVCategory('popular')} id='popular' className="btn btn-dark m-2">Most Popular</span>
    <span onClick={()=>changeTVCategory('top_rated')} id='top_rated' className="btn btn-dark m-2">Top Rated</span>
    <span onClick={()=>changeTVCategory('airing_today')} id='airing_today' className="btn btn-dark m-2">Airing Today</span>
    <span onClick={()=>changeTVCategory('on_the_air')} id='on_the_air' className="btn btn-dark m-2">On The Air</span>
  </div>

  <nav aria-label="..." className='d-flex justify-content-center align-items-center pagination'>
    <PaginationControl
    page={pageNumber} between={3} total={totalResult <= 10000? totalResult : 10000} limit={20} className="bg-dark" changePage={(page) => {changeNum(page)}} ellipsis={1}/>
  </nav>

    <div className="row m-auto w-100 g-3 pb-4 my-5">
      {tvList.map((tv,i)=> <div key={i} className="col-md-3 col-sm-3">
        <Link to={`/tvdetails/${tv.id}`}>
          {tv.poster_path == null ? 
          <div className='empty'></div> 
          : 
          <div className="item">
          <div className="borderImg position-relative">
            {tv.poster_path != null ? <img src={`https://image.tmdb.org/t/p/w500`+tv.poster_path} alt="" className="w-100" /> 
            : <img src={`https://image.tmdb.org/t/p/w500`+tv.backdrop_path} alt="" className="w-100 position-absolute" />}
            <div className="layer position-absolute">
                <div className="d-flex justify-content-center align-items-center">
                  <div className='w-100 text-center my-5'>
                    <i className="fa-solid fa-star h2 my-2"></i>
                    <p className='h2 my-2 fw-bold w-100'>{tv.vote_average.toFixed(1)} / 10</p>
                  </div>
                  <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                </div>
            </div>
          </div>
          <h3 className='h6 fw-bold'>{tv.name}</h3>
          <p className='h6'>{tv.first_air_date}</p>
        </div> }
        </Link>
      </div>)}
  </div>
  </> </div>}</>)
}

