import axios from 'axios'
import React,{ useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Slider from 'react-slick';
// import BasicFilm from 'react-film';
import avatarImg from "../../image/avatar.jpg"

export default function Home() {
  let[moviesList,setMoviesList]=useState([])           //each type of the request
  let [isLoading, setIsLoading] = useState(false)
  let[tvList,setTVList]=useState([])
  let[peopleList,setPeopleList]=useState([])
  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 2
  };

 useEffect(()=>{                            //using useEffect in mount stage to 1-send type to api_request 2-updating the corresponding function of the type
  getData(`movie`,setMoviesList)
  getData(`tv`,setTVList)
  getData(`person`,setPeopleList)
  document.title=`Cinema Box - Discover What You Want`

 },[])


  async function getData(type,callback){      // sending 2 parameters to the function as ( type responsble for the type of request) and (callback responsble for calling and updating the corresponding function in useeEffect) 
    setIsLoading(true)
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${type}/week?api_key=645e1d02e7ba893edf58577a4a104840`)
    setIsLoading(false)
    callback(data.results.slice(0,12))
  }        
  return ( <>{isLoading? <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><span className="mx-2">Loading</span></h1>:   
  <div className="container container-fluid container-sm my-5 pb-5"><>  



    <Link to="/movies">
        <div className="text-center">

          <h2 className='text-center'>Trending Movies<br/>To Watch<br/><span className='fw-bold h1'>Now</span></h2>
          <div className="borderSection my-2 w-75"></div>
          <div className="borderSection my-2 w-50"></div>
          <div className="borderSection my-2 w-25"></div>
        </div>
    </Link>
    <Slider {...settings}>
{moviesList.map((movie,i)=><div key={i}>
        <Link to={`/moviedetails/${movie.id}`}>
        {movie.poster_path == null ? 
          <div className='empty'></div> 
          :
          <div className="item mx-2">
            <div className="borderImg position-relative">
              <img src={`https://image.tmdb.org/t/p/w500`+movie.poster_path} alt="" className="w-100" />
              <div className="layer position-absolute">
                <div className="d-flex justify-content-center align-items-center">
                  <div className='w-100 text-center my-1'>
                    <i className="fa-solid fa-star h2 my-2"></i>
                    <p className='h2 my-2 fw-bold w-100'>{movie.vote_average.toFixed(1)} / 10</p>
                  </div>
                  <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                </div>
            </div>
            </div>
            <h3 className='h6 fw-bold'>{movie.title}</h3>
            <p className='h6'>{movie.release_date.slice(0,4)}</p>
          </div>}
        </Link>
      </div>)}
    </Slider>

    <div className="borderSection w-100"></div>

    <Link to="/tv">
        <div className="text-center">
        
        <h2 className='text-center'>Trending TV Shows<br/>To Watch<br/><span className='fw-bold h1'>Now</span></h2>
        <div className="borderSection my-2 w-75"></div>
        <div className="borderSection my-2 w-50"></div>
        <div className="borderSection my-2 w-25"></div>
        </div>
    </Link>
    <Slider {...settings}>
    {tvList.map((tv,i)=><div key={i}>
      <Link to={`/tvdetails/${tv.id}`}>
      {tv.poster_path == null ? 
          <div className='empty'></div> 
          :
        <div className="item mx-2">
          <div className="borderImg position-relative">
            <img src={`https://image.tmdb.org/t/p/w500`+tv.poster_path} alt="" className="w-100" />
            <div className="layer position-absolute">
                <div className="d-flex justify-content-center align-items-center">
                  <div className='w-100 text-center my-1'>
                    <i className="fa-solid fa-star h2 my-2"></i>
                    <p className='h2 my-2 fw-bold w-100'>{tv.vote_average.toFixed(1)} / 10</p>
                  </div>
                  <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                </div>
            </div>
          </div>
          <h3 className='h6 fw-bold'>{tv.name}</h3>
          <p className='h6'>{tv.first_air_date.slice(0,4)}</p>
        </div>}
        </Link>
      </div>)}
    </Slider>
    
    <div className="borderSection w-100"></div>
    
    <Link to="/people">
        <div className="text-center">
        
        <h2 className='text-center'>Trending Actors<br/>To Watch<br/><span className='fw-bold h1'>Now</span></h2>
        <div className="borderSection my-2 w-75"></div>
        <div className="borderSection my-2 w-50"></div>
        <div className="borderSection my-2 w-25"></div>
        </div>
    </Link>
    <Slider {...settings}>
    {peopleList.map((person,i)=><div key={i}>
      <Link to={`/peopledetails/${person.id}`}>
        <div className="item mx-2">
          <div className="borderImg position-relative">
              {person.profile_path != null ?<img src={`https://image.tmdb.org/t/p/w500`+person.profile_path} alt="" className="w-100" />:<img src={avatarImg} alt="" className="w-100" />}
              <div className="layer position-absolute">
                <div className="d-flex justify-content-center">
                    <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
                </div>
              </div>
          </div>
          <h3 className='h6 fw-bold'>{person.name}</h3>
        </div></Link>
      </div>)}
    </Slider>

  </></div>}</>)
}
