/* eslint-disable jsx-a11y/iframe-has-title */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import ShowMoreText from "react-show-more-text";
import { useParams,useNavigate,Link } from 'react-router-dom'
import imbd from '../../image/imbd.png'
import popularity from '../../image/popularity.png'
import movieImg from "../../image/movie.jpg"
import avatarImg from "../../image/avatar.jpg"

export default function TVDetails() {
    let {id} = useParams()           //to recive the id in the url
    let navigate = useNavigate();
    let[tv,setTV]=useState(null)
    let [isLoading, setIsLoading] = useState(false)
    let[tvVideo,setTVVideo]=useState(null)
    let[pageTitle,setPageTitle]=useState('')
    let[tvCreditsCast,setTVCreditsCast]=useState(null)
    let[tvCreditsCrew,setTVCreditsCrew]=useState(null)
    let[tvSimilar,setTVSimilar]=useState(null)
    let[showMoreCast,setShowMoreCast]=useState(6)
    let[showMoreCrew,setShowMoreCrew]=useState(6)
    let[showMoreSimilar,setShowMoreSimilar]=useState(6)
    let[showMoreTextCast,setShowMoreTextCast]=useState("Show More")
    let[showMoreTextCrew,setShowMoreTextCrew]=useState("Show More")
    let[showMoreTextSimilar,setShowMoreTextSimilar]=useState("Show More Similar")

    useEffect(()=>{
        getTVDetails(id)
        getTVVideo(id)
        getTVCreditCast(id)
        getTVCreditCrew(id)
        getTVSimilar(id)
        document.title=`${pageTitle}`   //to show the title in the window
    },[id,pageTitle])

    async function getTVDetails(tvId){
        setIsLoading(true)
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        setIsLoading(false)
        setTV(data)
        setPageTitle(data.name)
    }
    async function getTVVideo(tvId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}/videos?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        console.log(data.results)
        setTVVideo(data.results)
    }
    async function getTVCreditCast(tvId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}/credits?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        let unique = [...new Map(data.cast.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setTVCreditsCast(order)
    }
    async function getTVCreditCrew(tvId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}/credits?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
        let unique = [...new Map(data.crew.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setTVCreditsCrew(order)
    }
    async function getTVSimilar(tvId){
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}/similar?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US&page=1`)
        let unique = [...new Map(data.results.map((m) => [m.id, m])).values()];  // prevent of repeating objects
        let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
        setTVSimilar(order)
    }

    async function showMoreCastToggle(){
        if(showMoreCast === 6 && showMoreTextCast === "Show More"){
          setShowMoreCast(9999)
          setShowMoreTextCast("Show Less")
        }
        else{
          setShowMoreCast(6)
          setShowMoreTextCast("Show More")
        }
    }
    async function showMoreCrewToggle(){
        if(showMoreCrew === 6 && showMoreTextCrew === "Show More"){
          setShowMoreCrew(9999)
          setShowMoreTextCrew("Show Less")
        }
        else{
          setShowMoreCrew(6)
          setShowMoreTextCrew("Show More")
        }
    }
    async function showMoreSimilarToggle(){
        if(showMoreSimilar === 6 && showMoreTextSimilar === "Show More Similar"){
          setShowMoreSimilar(9999)
          setShowMoreTextSimilar("Show Less")
        }
        else{
          setShowMoreSimilar(6)
          setShowMoreTextSimilar("Show More Similar")
        }
    }


return (<>{isLoading? <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><pan className="mx-2">Loading</pan></h1>:
<>{tv != null?
<div className="container" style={{ backgroundImage : `linear-gradient(rgba(0, 0, 0, 0.338),rgba(0, 0, 0, 0.338)),url(https://image.tmdb.org/t/p/w500${tv.backdrop_path})`, backgroundRepeat:'no-repeat' ,backgroundSize:'100%'}}>

    <div className="row my-4">
        <div className="col-md-4 text-center">
            {tv.poster_path != null ? <div className="borderImgProfile">
                <img src={`https://image.tmdb.org/t/p/w500`+tv.poster_path} alt="" className="w-100" />
            </div> : <div className="borderImgProfile">
                <img src={`https://image.tmdb.org/t/p/w500`+tv.backdrop_path} alt="" className="w-100" />
            </div>}
            
            <a href={tv.homepage} target="_blank" rel="noreferrer" className="btn btn-warning px-5 py-1 my-3">Watch Now</a>
        </div>
        <div className="col-md-8 gy-3">
            <h3 className='h1 mx-1 my-2'>{tv.name}</h3>
            <p className='h2 mx-1 my-2'>{tv.first_air_date}</p>
            <p className='h5 mx-1 my-2'>"{tv.tagline}"</p>
            {tv.genres.map((e,i)=> <span key={i} className="btn btn-info mx-1 my-2">{e.name}</span>)}
            <div className="vote my-2 mx-1">
                <p className='h2'>{tv.vote_average.toFixed(1)} / 10</p>
                <p className='h5'>Total Votes : {tv.vote_count}</p>
            </div>
            {tv.overview !== false ? <div className="summary my-5">
                <p className='fw-bold'>Plot Summary</p>
                <ShowMoreText lines={3} more="Show more" less="Show less" className="content-css"><p>{tv.overview}</p></ShowMoreText>
            </div>:""}
            
            <div className="logo d-flex">
                <div className='logoImg m-2'>
                <a href={`https://www.imdb.com/title/${tv.imdb_id}/`} target="_blank" rel="noreferrer" title='IMDB Page'>
                    <img src={imbd} alt="" className='w-100' />
                </a>
            </div>
            <div className='logoImg m-2'>
                <img src={popularity} alt="" className='w-100 d-inline-block' title='Popularity' />
                {Math.floor(tv.popularity)}
            </div>
            </div>
        </div>
    </div>


{tvVideo !== null?<>
    {tvVideo.length > 0?<>
    <h2>Watch Trailer</h2>
{tvVideo.map((video,i)=>
<>{video.type==="Trailer" ? 
<div className='row'>
    <iframe className='col-md-10 mx-auto my-3' allowfullscreen="allowfullscreen" width="420" height="345" src={`https://www.youtube.com/embed/${video.key}?controls=1`} ></iframe>
</div>:""}</>)} 
</> :""}</>:""}

<div className="row g-3 my-4">
    
    {tvCreditsCast !== null ? <>
    {tvCreditsCast.length > 0 ?<h2>Cast</h2> :""}
        {tvCreditsCast.map((tvCast,i)=> 
            <Link to={`/peopledetails/${tvCast.id}`} key={i} className="col-md-2">
                
                <div className="item">
                    <div className="borderImg position-relative">
                        <img src={tvCast.profile_path !== null ? `https://image.tmdb.org/t/p/w500${tvCast.profile_path}` : avatarImg} alt="" className="w-100" />
                        <div className="layer position-absolute">
                            <div className="d-flex justify-content-center align-items-center">
                                <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
                            </div>
                        </div>
                    </div>
                    <h3 className='h6 fw-bold'>{tvCast.name}</h3>
                </div>
                
            </Link>
        ).slice(0,showMoreCast)}
        {tvCreditsCast.length > 6 ?
            <div className="row">
                <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreCastToggle}>{showMoreTextCast}</button>
            </div>
        :""}
    
    </> :""}

    {tvCreditsCrew !== null ? <>
    {tvCreditsCrew.length > 0 ? <h2>Crew</h2> : ""}
        {tvCreditsCrew.map((tvCrew,i)=>
            <Link to={`/peopledetails/${tvCrew.id}`} key={i} className="col-md-2">
                    <div className="item">
                    <div className="borderImg position-relative">
                        <img src={tvCrew.profile_path !== null ? `https://image.tmdb.org/t/p/w500${tvCrew.profile_path}` : avatarImg} alt="" className="w-100" />
                        <div className="layer position-absolute">
                            <div className="d-flex justify-content-center align-items-center">
                                <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
                            </div>
                        </div>
                    </div>
                    <h3 className='h6 fw-bold'>{tvCrew.name}</h3>
                    </div>
            </Link>
        ).slice(0,showMoreCrew)}
        {tvCreditsCrew.length > 6 ? 
            <div className="row">
                <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreCrewToggle}>{showMoreTextCrew}</button>
            </div>        
        :""}
    </>:""}

    {tvSimilar !== null ? <>
    {tvSimilar.length > 0 ? <h2>Watch Also</h2> : ""}
        {tvSimilar.map((tvSimilar,i)=>
            <Link key={i} to={`/tvdetails/${tvSimilar.id}`} className="col-md-2">
                
                    <div className="item">
                <div className="borderImg position-relative">
                    <img src={tvSimilar.poster_path !== null ? `https://image.tmdb.org/t/p/w500${tvSimilar.poster_path}` : movieImg} alt="" className="w-100" />
                    <div className="layer position-absolute">
                        <div className="d-flex justify-content-center align-items-center">
                            <div className='w-100 text-center my-1'>
                                <i className="fa-solid fa-star h2 my-2"></i>
                                <p className='h2 my-2 fw-bold w-100'>{tvSimilar.vote_average.toFixed(1)} / 10</p>
                            </div>
                            <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                        </div>
                </div>
                </div>
                <h3 className='h6 fw-bold'>{tvSimilar.title}</h3>
                <p className='h6'>{tvSimilar.first_air_date}</p>
                    </div>

            </Link>
        ).slice(0,showMoreSimilar)}
        {tvSimilar.length > 6 ? 
            <div className="row">
                <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreSimilarToggle}>{showMoreTextSimilar}</button>
            </div>         
        :""}
    </>:""}    
    
</div>

<footer className='row position-static bottom-0 justify-content-center'>
    <button className="btn btn-dark col-md-4 my-2" onClick={() => navigate(-1)}><i className="fa-sharp fa-solid fa-arrow-left"></i> Back</button>
</footer>
</div>:""}</>}</>)
}
