import { createContext,useState} from "react";


export let ShareCategory = createContext()

export function ShareCategoryProvider(props){
    let [movieCategoryType,setMovieCategoryType]=useState('popular')
    let [tvCategoryType,setTVCategoryType]=useState('popular')
    
    function changeMovieCategory(type){
        setMovieCategoryType(type)
    }
    function changeTVCategory(type){
        setTVCategoryType(type)
    }




    let contextValues={
        changeMovieCategory,
        movieCategoryType,
        changeTVCategory,
        tvCategoryType
    }
    return <ShareCategory.Provider value={contextValues}>{props.children}</ShareCategory.Provider>
}
