import axios from 'axios'
import React, {useState} from 'react'
import {useNavigate} from 'react-router-dom'
import Joi from 'joi'

export default function Register() {

let [user,setUser]=useState({
  first_name:'',
  last_name:'',
  age:'',
  email:'',
  password:''
})
let [errorApi,setErrorApi]=useState('')
let [errorList,setErrorList]=useState([])
let [loading,setLoading]=useState(false)
let navigate = useNavigate()
let [typeInput,setTypeInput]=useState('password')
let [title,setTitle]=useState('Show Password')
let [typeEye,setTypeEye]=useState('')

function addUser(e){
  let myUser={...user}
  myUser[e.target.name]=e.target.value  /* using deep copy of user to not affect on it while changing */
  setUser(myUser)
}
async function submitForm(e){
  e.preventDefault()   /* to prevent refresh & send data in URL */
  let valid = validData()
  if(valid.error == null){
    setLoading(true)
    let {data}=await axios.post('https://route-movies-api.vercel.app/signup',user)
    setLoading(false)
    if(data.message == 'success'){
      navigate('/login')
    }
    else{
      setErrorApi(data.message)
    }
  }
  else{
    setErrorList(valid.error.details)
  }
  // let err = errorList.map((el)=>el.message)
  // console.log(err.includes('"first_name"'))
}
function togglePassword(){
  if(typeInput==='password'){
      setTypeInput("text")
      setTypeEye("-slash fa-sharp")
      setTitle("Hide Password")
  }
  else{
      setTypeInput("password")
      setTypeEye("")
      setTitle("Show Password")
  }
}
function validData(){
  let schema = Joi.object({
    first_name:Joi.string().required().min(3).max(20).alphanum(),
    last_name:Joi.string().required().min(3).max(20).alphanum(),
    age:Joi.number().min(18).max(120).required(),
    email:Joi.string().email({tlds:{allow:['com','net']}}).required(),
    password:Joi.string().pattern(new RegExp(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,15}$/),'requirments')
  })
  return schema.validate(user,{abortEarly:false})
}
  return (    <div className="container height">    <>
    <h2>Registration</h2>
    {errorApi == ""? "" :<div className='alert alert-danger'>{errorApi.replace("citizen validation failed:","")}</div> }  
    <form onSubmit={submitForm}>   {/* using  onSubmit to use all submit properties*/}
      <div className="my-3">
        <label className='d-flex' htmlFor="first_name"><span className='me-4'>Firts Name</span>{errorList.length>0? errorList.map((el)=> <div className="text-info font-weight-bold">{el.path[0] == 'first_name' ? el.message.replace("_"," ") : ""}</div> ):""}</label>
        <input onChange={addUser} type="text" name='first_name' id='first_name' className='form-control bg-transparent text-white mt-2' />
      </div>
      <div className="my-3">
        <label className='d-flex' htmlFor="last_name"><span className='me-4'>Last Name</span>{errorList.length>0? errorList.map((el)=> <div className="text-info font-weight-bold">{el.path[0] == 'last_name' ? el.message.replace("_"," ") : ""}</div> ):""}</label>
        <input onChange={addUser} type="text" name='last_name' id='last_name' className='form-control bg-transparent text-white mt-2' />
      </div>
      <div className="my-3">
        <label className='d-flex' htmlFor="age"><span className='me-4'>Age</span>{errorList.length>0? errorList.map((el)=> <div className="text-info font-weight-bold">{el.path[0] == 'age' ? el.message.replace("_"," ") : ""}</div> ):""}</label>
        <input onChange={addUser} type="number" name='age' id='age' className='form-control bg-transparent text-white mt-2' />
      </div>
      <div className="my-3">
        <label className='d-flex' htmlFor="email"><span className='me-4'>Email</span>{errorList.length>0? errorList.map((el)=> <div className="text-info font-weight-bold">{el.path[0] == 'email' ? el.message : ""}</div> ):""}</label>
        <input onChange={addUser} type="email" name='email' id='email' className='form-control bg-transparent text-white mt-2' />
      </div>
      <div className="my-3">
        <label className='d-flex' htmlFor="password"><span className='me-4'>Password</span>{errorList.length>0? errorList.map((el)=> <div className="text-info font-weight-bold">{el.path[0] == 'password' ? el.message : ""}</div> ):""}</label>
        <div className='passwordContainer p-0 position-relative'>
          <input onChange={addUser} type={typeInput} name='password' placeholder='At least one uppercase, one lowercase, one number & special charcter (minmum 8 charcter and maximum 15)' id='password' className='form-control bg-transparent text-white mt-2 m-0' />
          <i className={`fa-solid fa-eye${typeEye} eye `} onClick={togglePassword} title={`${title}`}></i>
        </div>
      </div>
      {loading ? <button type='button' className="btn btn-outline-info">
        <i className="fa-solid fa-spinner fa-spin"></i></button> :<button type='submit' className="btn btn-outline-info">Regist</button> 
      }
    </form>
  </></div>)
}
