import axios from 'axios'
import React, { useEffect, useState } from 'react'
import ShowMoreText from "react-show-more-text";
import { useParams,useNavigate,Link } from 'react-router-dom'
import popularity from '../../image/popularity.png'
import avatarImg from "../../image/avatar.jpg"
import movieImg from "../../image/movie.jpg"



export default function PeopleDetails() {


  let {id} = useParams()
  let navigate = useNavigate();
  let[person,setPerson]=useState(null)
  let [isLoading, setIsLoading] = useState(false)
  let[personCreditCast,setPersonCast]=useState(null)
  let[pageTitle,setPageTitle]=useState('')
  let[showMore,setShowMore]=useState(6)
  let[showMoreText,setShowMoreText]=useState("Show More Related")
  
  useEffect(()=>{
  getPersonDetails(id)
  getPersonCast(id)
  document.title=`${pageTitle}`
  },[id,pageTitle])
  
  async function getPersonDetails(personId){
    setIsLoading(true)
    let {data} = await axios.get(`https://api.themoviedb.org/3/person/${personId}?api_key=8613e4e1776af4e8633cc311d67b3e09`)
    setIsLoading(false)
    setPerson(data)
    setPageTitle(data.name)
  }
  async function getPersonCast(personId){
      let {data} = await axios.get(`https://api.themoviedb.org/3/person/${personId}/combined_credits?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US`)
      let unique = [...new Map(data.cast.map((m) => [m.id, m])).values()];
      let order = unique.sort((a, b) => (a.popularity < b.popularity) ? 1 : -1) //order the array by a proprety to make it easier
      setPersonCast(order)
  }
  async function showMoreToggle(){
    if(showMore === 6){
      setShowMore(9999)
      setShowMoreText("Show Less")
    }
    else{
      setShowMore(6)
      setShowMoreText("Show More Related")
    }
  }
return (<>{isLoading? <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><pan className="mx-2">Loading</pan></h1>:   
<div className="container">   <>
  {person !== null?
  <div className="row my-4">
        <div className="col-md-4 text-center">
            <div className="borderImgProfile">
            {person.profile_path != null ?<img src={`https://image.tmdb.org/t/p/w500`+person.profile_path} alt="" className="w-100" />:<img src={avatarImg} alt="" className="w-100" />}
            </div>
            {person.imdb_id == null ? "":<a href={`https://www.imdb.com/name/${person.imdb_id}/`} target="_blank" rel="noreferrer" className="btn btn-warning px-5 py-1 my-3">IMDb Page</a>}
            {person.homepage == null ? "":<a href={person.homepage} target="_blank" rel="noreferrer" className="btn btn-warning px-5 py-1">Homepage</a>}
        </div>
        <div className="col-md-8 gy-3">
            <h3 className='h1 mx-1 my-2'>{person.name}</h3>
            <p className='h2 mx-1 my-2 fw-bold'>Birthday : <span className=' fw-normal'>{person.birthday}</span></p>
            <p className='h5 mx-1 my-2 text-muted'>{person.place_of_birth}</p>
            {person.deathday == null ?"":<p className='h2 mx-1 my-2 text-muted'>Deathday : <span className='text-white'>{person.deathday}</span></p>}
            <p className='h5 mx-1 my-3 fw-bold'>Known For : <span className='text-white fw-normal'>{person.known_for_department}</span></p>
            {person.also_known_as !== false ? <div className="knowAs my-5">
                <p className='fw-bold'>Also Known As</p>
                {person.also_known_as.map((e,i)=> <span key={i} className="btn btn-dark mx-1 my-1">{e}</span>).slice(0,5)}
            </div> : ""}
            {person.biography !== false ? <div className="bio my-5">
                <p className='fw-bold'>About</p>
                <ShowMoreText lines={2}  more="Show more" less="Show less" className="content-css">
                    <p>{person.biography}</p>
                </ShowMoreText>
            </div>: ""}          
            
            <div className='logoImg m-2'>
                <img src={popularity} alt="" className='w-100 d-inline-block' title='Popularity' />
                {Math.floor(person.popularity)}
            </div>
        </div>
  </div>
  :""}

{personCreditCast != null ? <>
{personCreditCast.length > 0 ?<>

  <div className="row g-3 my-4">
    <h2>Known For</h2>
    {personCreditCast.map((personCast,i)=>
          <div key={i} className="col-md-2">
            {personCast.popularity > 1 ? 
              <Link to={personCast.media_type === "movie" ? `/moviedetails/${personCast.id}` : `/tvdetails/${personCast.id}`}>
                <div className="item">
                <div className="borderImg position-relative">
                    <img src={personCast.poster_path !== null ? `https://image.tmdb.org/t/p/w500${personCast.poster_path}` : movieImg} alt="" className="w-100" />
                    <div className="layer position-absolute">
                        <div className="d-flex justify-content-center align-items-center">
                            <div className='w-100 text-center my-1'>
                                <i className="fa-solid fa-star h2 my-2"></i>
                                <p className='h2 my-2 fw-bold w-100'>{personCast.vote_average.toFixed(1)} / 10</p>
                            </div>
                            <span className='btn btn-dark position-absolute seeDetails m-auto'>View Details</span>
                        </div>
                </div>
                </div>
                <h3 className='h6 fw-bold'>{personCast.media_type === "movie" ? personCast.title : personCast.name}</h3>
                <p className='h6'>{personCast.media_type === "movie" ? personCast.release_date.slice(0,4) : personCast.first_air_date}</p>
                </div>
              </Link>
              : <div className='empty'></div>
            }
          </div>
    ).slice(0,showMore)}
  </div>

  {personCreditCast.length > 6 ? 
    <div className="row">
  <button className='btn btn-dark col-md-4 m-auto my-2' onClick={showMoreToggle}>{showMoreText}</button>
    </div>
  : ""}

</> :""}</>:""}


  <footer className='row position-static bottom-0 justify-content-center'>
  <button className="btn btn-dark col-md-4 my-2" onClick={() => navigate(-1)}><i className="fa-sharp fa-solid fa-arrow-left"></i> Back</button>
  </footer>
</> </div>}</>)
}