import './App.css';
import {createBrowserRouter,RouterProvider, Navigate, createHashRouter} from 'react-router-dom';
import Masterlayout from './Components/Masterlayout/Masterlayout';
import Notfound from './Components/Notfound/Notfound';
import Home from './Components/Home/Home';
import Movies from './Components/Movies/Movies';
import About from './Components/About/About';
import People from './Components/People/People';
import TV from './Components/TV/TV';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import { useState, useEffect } from 'react';
import jwt_decode from 'jwt-decode';
import MovieDetails from './Components/MovieDetails/MovieDetails';
import TVDetails from './Components/TVDetails/TVDetails';
import PeopleDetails from './Components/PeopleDetails/PeopleDetails';
import { ShareCategoryProvider } from './Components/Navbar/Category';

function App() {
  let [user,setUser]=useState(null)
  useEffect(()=>{                    // we use useEffect in mount stage to set if condition on loacalstorage
    if(localStorage.getItem("token") != null){  //to avoid not reading the user data when refreshing or going to the page by url
      saveUser()
    }
  },[])
  function saveUser(){               // we call this function after we certain that the user has been saved in localstorage in Login.jsx so we send this function as props to Login.jsx
    let token = localStorage.getItem("token")             //get token from localstorage which contain the user data encoded
    let data = jwt_decode(token)                 //decode the user data from token
    setUser(data)
  }
  function logOut(){
    localStorage.removeItem("token")
    setUser(null)
    return <Navigate to='/login'/>
  }
  function ProtecteUser(props){         //to avoid going to any page without login , // we use Uppercase in the name of function to use it as a tag , when we use it as a tag the props of it contain every children tags in the main tage of the function
    if(localStorage.getItem("token") == null){
      return <Navigate to='/login'/>   //returning to login page if localstorage = null
    }
    else{
      return props.children          // using the props to specify the path as a children of it
    }
  }
let Routes = createHashRouter([                /* we need to pass Routes to  RouterProvider*/
  {path:'/',element: <Masterlayout user={user} logOut={logOut}/>, /* when path is empty go to Masterlayout, in error case go to Notfound, childrens come as array of object */ 
    children:[       
      {path:'/',element:<ProtecteUser><Home/></ProtecteUser>},                                                                              
      {path:'home',element:<ProtecteUser><Home/></ProtecteUser>},                                                                              
      {path:'about',element:<ProtecteUser><About/></ProtecteUser>},                                                                              
      {path:'movies',element:<ProtecteUser><Movies/></ProtecteUser>},                                                                              
      {path:'moviedetails/:id',element:<ProtecteUser><MovieDetails/></ProtecteUser>},                                                                              
      {path:'tvdetails/:id',element:<ProtecteUser><TVDetails/></ProtecteUser>},                                                                              
      {path:'peopledetails/:id',element:<ProtecteUser><PeopleDetails/></ProtecteUser>},                                                                              
      {path:'people',element:<ProtecteUser><People/></ProtecteUser>},                                                                              
      {path:'tv',element:<ProtecteUser><TV/></ProtecteUser>},                                                                              
      {path:'login',element:<Login saveUser={saveUser}/>},         // sending the saving_user function to Login.jsx as props                                                                             
      {path:'register',element:<Register/>},
      {path:'*',element:<Notfound/>}                                                                             
    ]                                                                               
  }              
])


  return (
    <ShareCategoryProvider>
       <RouterProvider router={Routes}/>       {/* // we passed Routes to  RouterProvider as props  */}
    </ShareCategoryProvider>
  );
}

export default App;
