import axios from 'axios'
import React, {useState} from 'react'
import {useNavigate} from 'react-router-dom'
import Joi from 'joi'

export default function Login({saveUser}) {
  let [user,setUser]=useState({
    email:'',
    password:''
  })
  let [errorApi,setErrorApi]=useState('')
  let [errorList,setErrorList]=useState([])
  let [loading,setLoading]=useState(false)
  let navigate = useNavigate()
  let [typeInput,setTypeInput]=useState('password')
  let [title,setTitle]=useState('Show Password')
  let [typeEye,setTypeEye]=useState('')
  
  function addUser(e){
    let myUser={...user}
    myUser[e.target.name]=e.target.value  /* using deep copy of user to not affect on it while changing */
    setUser(myUser)
  }
  async function submitForm(e){
    e.preventDefault()   /* to prevent refresh & send data in URL */
    let valid = validData()
    if(valid.error == null){
      setLoading(true)
      let {data}=await axios.post('https://route-movies-api.vercel.app/signin',user)
      setLoading(false)
      if(data.message == 'success'){
        navigate('/home')
        localStorage.setItem("token",data.token)
        saveUser()                  // we call saveUser() here to make sure that the data has been saved
      }
      else{
        setErrorApi(data.message)
        console.log(errorApi);
      }
    }
    else{
      setErrorList(valid.error.details)
      console.log(errorList);
    }
    // let err = errorList.map((el)=>el.message)
    // console.log(err.includes('"first_name"'))
  }
  function togglePassword(){
    if(typeInput==='password'){
        setTypeInput("text")
        setTypeEye("-slash fa-sharp")
        setTitle("Hide Password")
    }
    else{
        setTypeInput("password")
        setTypeEye("")
        setTitle("Show Password")
    }
  }
  function validData(){
    let schema = Joi.object({
      email:Joi.string().email({tlds:{allow:['com','net']}}).required(),
      password:Joi.string().pattern(new RegExp(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,15}$/),'" Minimum eight and maximum 15 characters, at least one uppercase letter, one lowercase letter, one number and one special character "')
    })
    return schema.validate(user,{abortEarly:false})
  }




    return (    <div className="container height">    <>
      <h2>Login</h2>
      {errorList == ""? "" :<div className='alert alert-danger'>Email Or Password Is Wrong</div>}  
      <form onSubmit={submitForm}>   {/* using  onSubmit to use all submit properties*/}
        <div className="my-3">
          <label className='d-flex' htmlFor="email"><span className='me-4'>Email</span></label>
          <input onChange={addUser} type="email" name='email' id='email' className='form-control bg-transparent text-white mt-2' />
        </div>

        <div className="my-3">
          <label className='d-flex' htmlFor="password"><span className='me-4'>Password</span></label>
          <div className='passwordContainer p-0 position-relative'>
            <input onChange={addUser} type={typeInput} name='password' id='password' className='form-control bg-transparent text-white mt-2 m-0' />
            <i className={`fa-solid fa-eye${typeEye} eye `} onClick={togglePassword} title={`${title}`}></i>
          </div>
        </div>

        {loading ? <button type='button' className="btn btn-outline-info">
          <i className="fa-solid fa-spinner fa-spin"></i></button> :<button type='submit' className="btn btn-outline-info">Login</button> 
        }
      </form>
    </></div>)
}
