import React, { useContext } from 'react'
import {Link,NavLink} from 'react-router-dom'
import { ShareCategory } from './Category'

export default function Navbar({user,logOut}) {
  let {changeMovieCategory,changeTVCategory} = useContext(ShareCategory)

  
  return (<>
  <nav className="navbar navbar-expand-lg navbar-dark bg-transparent">
  <Link className="navbar-brand" to='/'>Cinema Box</Link>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    {user != null ? 
    <>
    <ul className="navbar-nav me-auto">
      <li className="nav-item">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='home'>Home</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='about'>About</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='people'>People</NavLink>
      </li>
      <li className="nav-item dropdown">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='tv'>Tv Show
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" onClick={()=>changeTVCategory('popular')} to='tv' >Most Popular</Link></li>
            <li><Link className="dropdown-item" onClick={()=>changeTVCategory('top_rated')} to='tv' >Top Rated</Link></li>
            <li><Link className="dropdown-item" onClick={()=>changeTVCategory('airing_today')} to='tv' >Airing Today</Link></li>
            <li><Link className="dropdown-item" onClick={()=>changeTVCategory('on_the_air')} to='tv' >On The Air</Link></li>
          </ul>
        </NavLink>
      </li>
      <li className="nav-item dropdown">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='movies'>Movies
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" onClick={()=> changeMovieCategory('popular')} to='movies' >Most Popular</Link></li>
            <li><Link className="dropdown-item" onClick={()=> changeMovieCategory('top_rated')} to='movies' >Top Rated</Link></li>
            <li><Link className="dropdown-item" onClick={()=> changeMovieCategory('upcoming')} to='movies' >Upcoming</Link></li>
            <li><Link className="dropdown-item" onClick={()=> changeMovieCategory('now_playing')} to='movies' >Playing Now</Link></li>
          </ul>
        </NavLink>
      </li>
    </ul>
    <ul className="navbar-nav ms-auto">
      <li className="nav-item">
        <span className="nav-link"><i className="px-2 fa-solid fa-user"></i>{user.first_name}</span>
      </li>
      <li className="nav-item">
        <span onClick={logOut} className="nav-link">Logout</span>
      </li>
    </ul></> : 


    <ul className="navbar-nav ms-auto">
      <li className="nav-item">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='register'>Regist</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className={({isActive})=> isActive? "nav-link active":"nav-link"} to='login'>Login</NavLink>
      </li>
    </ul>}
    
    
  </div>
</nav>
  </>)
}
