import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { PaginationControl } from 'react-bootstrap-pagination-control';
import { Link } from 'react-router-dom'
import $ from 'jquery'
import avatarImg from "../../image/avatar.jpg"

export default function People() {
  let [peopleList,setPeopleList] = useState([])
  let [isLoading, setIsLoading] = useState(false)
  let [pageType,setPageType] = useState('popular')
  let [pageNumber,setPageNumber] = useState(1)
  let [totalResult,setTotalResult ]= useState(1)
  
  useEffect(()=>{
    getData(pageNumber,pageType)
    document.title=`People`
  },[pageNumber,pageType])

  async function getData(pN=pageNumber,pT=pageType){
    setIsLoading(true)
    let {data} = await axios.get(`https://api.themoviedb.org/3/person/${pT}?api_key=645e1d02e7ba893edf58577a4a104840&language=en-US&page=${pN}`)
    setIsLoading(false)
    setPeopleList(data.results)
    setTotalResult(data.total_results)
  }
  async function search(e){
    let value = e.target.value
    if (value !== ''){
      let {data} = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=8613e4e1776af4e8633cc311d67b3e09&language=en-US&query=${value}&page=1&include_adult=false`)
      setPeopleList(data.results);
    $(".pagination").hide(300)
    }
    else{
      getData()
      $(".pagination").show(500)
    }
  }

  function changeNum(page){
    setPageNumber(page)
    getData(page)
  }

  return (<>{isLoading? <h1 className='w-100  position-absolute bg-transparent loading d-flex justify-content-center align-items-center'><div><i className="fa-solid fa-spinner fa-spin"></i></div><pan className="mx-2">Loading</pan></h1>:
      <div className="container container-fluid container-sm">   <>
    <input className='form-control bg-transparent text-white w-75 my-5 mx-auto' onChange={search} type="text" placeholder='Search For Your Actor...'/>

  <nav aria-label="..." className='d-flex justify-content-center align-items-center pagination'>
    <PaginationControl
    page={pageNumber} between={3} total={totalResult <= 10000? totalResult : 10000} limit={20} className="bg-dark" changePage={(page) => {changeNum(page)}} ellipsis={1}/>
  </nav>

  <div className="row m-auto w-100 g-3 pb-4 my-5">
{peopleList.map((person,i)=><div key={i} className="col-md-3 col-sm-4 col-xl-2">
  <Link to={`/peopledetails/${person.id}`}>
    <div className="item">
    <div className="borderImg position-relative">
    {person.profile_path != null ?<img src={`https://image.tmdb.org/t/p/w500`+person.profile_path} alt="" className="w-100" />:<img src={avatarImg} alt="" className="w-100" />}
      <div className="layer position-absolute">
          <div className="d-flex justify-content-center align-items-center">
            <span className='btn btn-dark position-absolute seeDetails m-auto'>View Profile</span>
          </div>
      </div>
    </div>
    <h3 className='h6 fw-bold'>{person.name}</h3>
  </div>
  </Link>
</div>)}
</div>
  </> </div>}</>)
}
